.home-wrapper {
    text-align: center;
    min-height: 100vh;
  
    position: relative;
   
  }
  .content-title{
    font-family: 'Courier';
    color:rgb(51, 235, 66);
    font-size: 24px;
    margin-top: 24px;
    margin-bottom:2px;
  }
  .nick-name{
    font-family: 'Courier';
    color:rgb(247, 73, 51);
    font-size: 32px;
    margin-top:4px;
    font-weight: 700;
  }
  .support-letter {
    font-family: 'Courier';
    color:rgb(233, 245, 67);
    font-size: 18px;
    margin:0;
  }
  .home-page{
    background-image: linear-gradient(to bottom,#1a1f1e , #131b1b);
   
    box-sizing: content-box;
    overflow: hidden;
    
    display: flex;
    justify-content: center;
  }